import React, { Component } from 'react';
export default class Resume extends Component {
  render() {
 
    return (
      <React.Fragment>
      <section id="resume">
        {/* Education
      ----------------------------------------------- */}
        <div className="row education">
          <div className="three columns header-col">
            <h1><span>Education</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>Melbourne Polytechnic</h3>
                <p className="info">Bachelor of Information Technology - Software Development Major<span>•</span> <em className="date">November 2020</em></p>
                <p className="desc">
                With instruction from industry experts, I have analysed, designed, implemented and managed complex information systems including Enterprise
                Resource Planning (ERP) and Business Intelligence and Software Development. I have also create software for a wide range of applications.
                Melbourne Polytechnic students benefit from our partnership with multinational analytics software developer, SAS Institute Inc, as Bachelor
                 of Information Technology students also have the opportunity to receive the globally recognised “Information Technology Analytics”
                  qualification. This is a globally recognised professional industry certification for eligible students on successful completion of
                   specific units.
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>Melbourne Polytechnic</h3>
                <p className="info">Diploma of Information Technology <span>•</span> <em className="date">December 2018</em></p>
                <p className="desc">
                I have learned fundamental skills across a range of IT disciplines. This includes software development, web design, 
                server administration, virtualisation, networking, database design and development, IT project management and systems analysis.
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Education */}
        {/* Work
      ----------------------------------------------- */}
        <div className="row work">
          <div className="three columns header-col">
            <h1><span>Work</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>Mobile Tyre Shop</h3>
                <p className="info">IT Support <span>•</span> <em className="date">Jun 2020 - Present</em></p>
                <p className="desc">
                Providing assistance with stock and prices upload, creation of automated tasks with Microsoft Power Automate,
                 creation and update of
                product pages on Wordpress, administration of APIs and data analysis.
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>Greenfields- Albert Park</h3>
                <p className="info">Events Supervisor <span>•</span> <em className="date">Jan 2019 - Present</em></p>
                <p className="desc">
                 Development of interpersonal skills, communication, leadership, time management, delegation and prioritization.
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Work */}
        {/* Skills
      ----------------------------------------------- */}
        <div className="row skill">
          <div className="three columns header-col">
            <h1><span>Skills</span></h1>
          </div>
          <div className="nine columns main-col">
            <p className="desc"> The following skills were developed within the Bachelor and Diploma courses and self-teaching skills.
            </p>
            <div className="bars">
              <ul className="skills">
                <li><span className="bar-expand illustrator" /><em>Angular</em></li>
                <li><span className="bar-expand photoshop" /><em>Typescript</em></li>
                <li><span className="bar-expand wordpress" /><em>Wordpress</em></li>
                {/* <li><span className="bar-expand css2" /><em>CSS</em></li> */}
                <li><span className="bar-expand wordpress" /><em>HTML5</em></li>
                <li><span className="bar-expand html5" /><em>Nativescript</em></li>
              </ul>
            </div>{/* end skill-bars */}
          </div> {/* main-col end */}
        </div> {/* End skills */}
      </section>
      </React.Fragment>
    );
  }
}