import React, { Component } from 'react';
import Typical from 'react-typical'
export default class About extends Component {
  render() {
    return (
       <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/profilepic.JPG"/>
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <Typical
        steps={['I have recently graduated in Information technology and through-out my studies I have been working as venue supervisor and as IT support.', 800,'I have recently graduated in Information technology and through-out my studies I have been working as venue supervisor and as IT support. As a venue supervisor, I have improved my interpersonal skills, my communication, teamwork and leadership. Working as IT support has made me develop skills in problem solving and networking.',600,'I have recently graduated in Information technology and through-out my studies I have been working as venue supervisor and as IT support. As a venue supervisor, I have improved my interpersonal skills, my communication, teamwork and leadership. Working as IT support has made me develop skills in problem solving and networking. I have also developed an app prototype for a health insurance company called UnityHealth focused on medicine interactions and administration.',500]}
        loop={1}
        wrapper="p"
      
      />
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>Filipe Guimaraes</span><br />
                  <span>3 Kingston Road<br />
                    Heatherton, VIC 3202 AU
                  </span><br />
                  <span>+61 410 462 502</span><br />
                  <span>filipefsg98@gmail.com</span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href="https://drive.google.com/file/d/1pmqGY6uwqmdnAFFNO9z_TB2vbNAfaCaK/view?usp=sharing" className="button"><i className="fa fa-download" />Download Resume</a>
                </p>
              </div>
            </div> {/* end row */}
          </div> {/* end .main-col */}
        </div>
      </section>
    );
  }
}